////
/// Pullquote block.
///
/// @group  Blocks
/// @author Bryan Hoffman <bryan@spigotdesign.com>

////

.wp-block-pullquote {
	padding:    3em 0;
	text-align: center;

	&.alignleft,
	&.alignright {
		max-width: content-width() / 2;
	}

	cite,
	footer {
		position: relative;
	}
}



